@import './../../common/sass/variables';
@import '../../common/sass/media.scss';

.navBarContainer {
  height: 100%;
  display: flex;
  width: 100%;
  @include d-media(xs) {
    position: fixed;
    bottom: 0;
    height: auto;
    right: 0;
    background: black;
    a,
    .logout {
      padding: 19px 0;
      font-size: 19px;
      color: white;
      &:hover {
        font-size: 25px;
        color: #6b8e23;
      }
      &.activeLink {
        background: $buttonBackground;
        &:hover {
          font-size: 25px;
          color: white;
        }
      }
    }
  }

  a {
    height: 100%;
    text-decoration: none;
    color: $Text;
    font-family: $QuicksandFont;
    display: flex;
    flex-grow: 1;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
  }
  .active:before {
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    width: 100%;
    background: black;
    height: 100%;
    opacity: 0.4;
    z-index: -1;
    border-bottom: 3px solid $backgr;
  }
}
.logout {
  cursor: pointer;
  height: 100%;
  text-decoration: none;
  color: $Text;
  font-family: $QuicksandFont;
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
}
